
h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

ul,
ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}
html, body {
  overflow-x: hidden;
}
body {
  box-sizing: border-box;
	font-family: "Open Sans", sans-serif;
	outline: 0;
  scroll-behavior: smooth;
  &.modal-open {
    overflow: hidden;
  }
}
